body {
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
  background: #f2f2f2;
}

.container {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.card {
  background: white;
  padding: 40px;
  border-radius: 15px;
  width: 480px;
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.1);
  text-align: center;
}

.card h1 {
  color: #1e6cff;
  margin-bottom: 10px;
}

.card p {
  color: #555;
  margin-bottom: 25px;
}

.btn {
  display: block;
  padding: 15px;
  margin: 10px 0;
  border-radius: 8px;
  color: white;
  text-decoration: none;
  font-weight: bold;
  transition: transform 0.2s, opacity 0.2s;
}

.btn:hover {
  transform: scale(1.02);
  opacity: 0.9;
}

.blue {
  background: #176bff;
}

.green {
  background: #1e8e5a;
}

.dark {
  background: #1f2428;
}

.darker {
  background: #1b1f23;
}